function fMoney (s, n) {
  n = n > 0 && n <= 20 ? n : 2
  s = parseFloat((s + '').replace(/[^\d\.-]/g, '')).toFixed(n) + ''
  let l = s.split('.')[0].split('').reverse()
  let r = s.split('.')[1]
  let t = ''
  for(let i = 0; i < l.length; i ++ ) {
    t += l[i] + ((i + 1) % 3 == 0 && (i + 1) != l.length ? ',' : '')
  }
  return t.split('').reverse().join('') + '.' + r
}

function toDateTime (date, time) {
  if (!date) return ''
  date = date.toString()
  time = time ? time.toString() : ''
  let str = `${date.substr(0, 4)}-${date.substr(4, 2)}-${date.substr(6, 2)}`
  if (date.length == 14) {
    str += ` ${date.substr(8, 2)}:${date.substr(10, 2)}:${date.substr(12, 2)}`
  } else if (date.length == 6) {
    str = `${date.substr(0, 2)}:${date.substr(2, 2)}:${date.substr(4, 2)}`
  } else if (time) {
    str += ` ${time.substr(0, 2)}:${time.substr(2, 2)}:${time.substr(4, 2)}`
  }
  return str
}

function dateFormatFn(date){
	Y = date.getFullYear() + '-';
	M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
	D = date.getDate() + ' ';
let newDate = Y+M+D
return newDate
}

function fMoney2 (m) {
  return parseFloat(m).toFixed(2)
}

// 
function fMoney3(money) {
  //
  let cnNums = new Array('', '', '', '', '', '', '', '', '', '');
  let cnIntRadice = new Array('', '', '', '');//
  let cnIntUnits = new Array('', '', '', '');//
  let cnDecUnits = new Array('', '', '', ''); //
  let cnInteger = '';//
  // let
  let cnIntLast = ''; //
  let int;//
  let dot; //
  let chineseStr = ''; //
  let parts;    //
  if (money == '') { return ''; }
  //
  money = money.toString();
  if (money.indexOf('.') == -1) {
      int = money;
      dot = '';
  } else {
      parts = money.split('.');//
      int = parts[0];
      dot = parts[1].substr(0, 4);//
  }
  //
  if (parseInt(int, 10) > 0) {
      let zeroCount = 0;
      let IntLen = int.length;
      for (let i = 0; i < IntLen; i++) {//4
          let n = int.substr(i, 1);
          let p = IntLen - i - 1;
          let q = p / 4;
          let m = p % 4;
          if (n == '0') {
          zeroCount++;
          } else {
          if (zeroCount > 0) {
              chineseStr += cnNums[0];
          }
          //
          zeroCount = 0;
          chineseStr += cnNums[parseInt(n)] + cnIntRadice[m];
          }
          if (m == 0 && zeroCount < 4) {
          chineseStr += cnIntUnits[q];
          }
      }
      chineseStr += cnIntLast;
  }
  //
  if (dot != '') {
      let decLen = dot.length;
      for (let i = 0; i < decLen; i++) {
          let n = dot.substr(i, 1);
          if (n != '0') {
          chineseStr += cnNums[Number(n)] + cnDecUnits[i];
          }
      }
  }
  // if (chineseStr == '') {// 0
  //     chineseStr += cnNums[0] + cnIntLast + cnInteger;
  // }
  // else if (dot == '') {//
      chineseStr += cnInteger;
  // }
  return chineseStr;
}

function orderreleaseType (sts) {
  const status = {
    '0': '',
    '1': '',
  }
  return status[sts] || ''
}

function auditStatus (sts) {
  const status = {
    '1': {text: '', bgColor: '#FCF0E5',textColor: '#E7BB8B'},
    '2': {text: '', bgColor: '#E0F6EF',textColor: '#64C8A8'},
    '4': {text: '', bgColor: '#FCEBE9',textColor: '#D07D76'},
  }
  return status[sts] || {text: '', bgColor: '#333', textColor: '#666'}
}

function businessType (sts) {
  const status = {
    '0': '',
    '1': '',
    '2': '',
    '3': '',
  }
  return status[sts] || ''
}

function operationType (sts) {
  const status = {
    '0': '',
    '1': '',
    '2': '',
    '3': '',
    '4': '',
  }
  return status[sts] || ''
}

function removeByValue(arr, val) {
  for(var i=0; i<arr.length; i++) {
      if(arr[i] == val) {
      arr.splice(i, 1);
      break;
      }
  }
}

// 
function objectValueAllEmpty(object){
	var isEmpty = true;
	Object.keys(object).forEach(function(x) {
	    if(object[x] != null && object[x] != ""){
		    isEmpty = false;
	    }
	});
    if(isEmpty){//
	    return true;
    }
	return false;
}

// 
function debounce(func, wait) {
  let timer;
  return function() {
      let context = this; 
      let args = arguments; 
      if (timer) clearTimeout(timer);
      let callNow = !timer;
      timer = setTimeout(() => {
      timer = null;
      }, wait)
      if (callNow) func.apply(context, args);
  }
}

export default{
  fMoney,
  fMoney2,
  fMoney3,
  toDateTime,
  dateFormatFn,
  orderreleaseType,
  auditStatus,
  businessType,
  operationType,
  removeByValue,
  objectValueAllEmpty,
  debounce
}